.card{
    background-color: #fff;
    width: 100%;
    /* margin: 10px; */
    height: 40vh;
    /* padding:px 0px; */
    background-color: #35494d;
    border-radius: 20px;
    border:1px solid wheat;
    box-shadow: 0px 10px 13px rgba(0, 0, 0, 0.1);
}

span{
    font-size: 15px;
    width: 100%;
    padding: 10px;
}

.temp span{
    font-size: 20px;
    padding: 12px;
    width: 100%;
}
.icon span{
    padding: 8px;
    margin-top: 2px;
}

@media screen and (max-width: 680px) {
    .card{
        margin-top: 45px;
        height: 45vh;
    }
    span{
        padding: 12px;
    }
    .temp span{
        padding: 15px;
    }
    /* .icon{
        padding: 10px;
    } */
   
}