.App {
  text-align: center;
  width: 80%;
  height: auto;
  margin: auto;
  height: auto;
  box-shadow: 0px 10px 13px rgba(0, 0, 0, 0.1);
  background-color: #212e30;
  color: rgb(204, 197, 197);
}

.logo{
  margin-top: 0px;
  text-transform: uppercase;
  background-color: #405b5f;
  padding: 15px;
  color: wheat;
  /* margin: 10px; */
  width: 50%;
  text-align: center;
  margin-left: 25%;
  border-bottom-left-radius: 100px;
  border-bottom-right-radius: 100px;
  box-shadow: 0px 10px 13px rgba(0, 0, 0, 0.1);
}
#inputId{
  width: 60%;
  height: 45px;
  background-color: #35494d;
  border: 1px solid wheat;
  box-shadow: 0px 10px 13px rgba(0, 0, 0, 0.1);
  padding: 20px;
  border-radius: 25px;
  color: rgb(204, 197, 197);
}
#inputId:focus{
  outline: none;
}
#main{
  width: 100%;
  margin-top: 10px;
  display: flex;
  justify-content: space-around;
}
#searchbtn{
  width: 60px;
  /* height: 40px; */
  border: 1px solid wheat;
  padding: 10px;
  color: wheat;
  margin: 0px 10px;
  /* box-shadow: 0px 10px 13px rgba(0, 0, 0, 0.1); */
  /* border: none; */
  cursor: pointer;
  /* color: rgb(204, 197, 197); */
  background-color: #35494d;
  border-radius: 20px;
}
#searchbtn:hover{
  background-color:wheat;
  color: #35494d;
}
#details{
  /* height: 100vh; */
  /* flex: 1; */
  width: 80%;
  height: 65vh;
  border: 1px solid wheat ;
  padding: 50px 20px;
  border-radius: 20px;
  margin-bottom: 25px;
  background-color: #35494d;
  box-shadow: 0px 10px 13px rgba(0, 0, 0, 0.1);
  /* background-color: #fff; */
}
#cityName{
  font-family: sans-serif;
  font-size: 40px;
  text-transform: uppercase;
}
#temp{
  font-size: 60px;
}

#icon{
  width: 120px;
  height: 120px;
}

#left{
  /* margin-top: 30px; */
  /* flex: 1; */
  width: 75%;
  margin-top: 18px;
  height: 65vh;
}
#left p{
  padding: 25px;
  font-size: 20px;
  border-bottom: 2px dotted wheat;
  border-bottom-right-radius: 15px;
  border-bottom-left-radius: 15px;
}
.err{
  font-size:20px;

}

#right{
  /* margin-top: 30px; */
  /* flex: 1; */
  margin-top: 18px;
  width: 75%;
  height: 65vh;
} 

.foreCast{
  display: flex;
  box-sizing: border-box;
}
.footer{
  margin-top:2px;
  margin-bottom: 2px;
  height: 40px;
  width: 80%;
  /* margin: auto; */
  color: antiquewhite;
  background-color: #121516;
  box-shadow: 0px 10px 13px rgba(0, 0, 0, 0.1);
}
.footer p{
  margin-top: -8px;
  color: wheat;
}

@media screen and (max-width: 680px) {
  .App{
    width: 100%;
  }
  #main {
    display: inline-block;
  }
  .logo{
    width: 100%;
    margin-left: 0px;
    padding: 10px;
  }
  #details{
    width: 95%;
    height: 70vh;

  }
#inputId{
  margin-top: 20px;
  width: 70%;
}
  #searchbtn{
    padding: 8px;
    border-radius: 40px;
  }
  .footer{
    
    /* height: 0px; */
    width: 100%;
  }
  .footer p{
    margin-top: -8px;
  }
  #left{
    width: 100%;
  }
  #right{
    width: 100%;
  }
  .foreCast{
    /* margin-top: 30px; */
    display: inline-block;
    width: 80%;
  }
}